@use '../global/use.scss';

.main-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 23px;

  @include use.media-tablet {
    flex-direction: row;
    padding: 36px 76px 36px 60px;
    justify-content: space-between;
  }

  @include use.media-desktop {
    padding: 36px 136px 36px 119px;
  }

  &__tag {
    @include use.font-large;
    text-transform: uppercase;
    text-decoration: none;
    color: use.$color-black;
    margin-bottom: 34px;
    max-width: 136px;

    @include use.media-tablet {
      margin: 0;
    }
  }

  &__social-list {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    column-gap: 6px;
    padding: 0;
    margin: 0;
    margin-bottom: 40px;
    max-width: 264px;
    justify-content: center;

    @include use.media-tablet {
      margin: 0;
    }
  }

  &__social-item {
    background-color: use.$color-aqua;
    padding: 0;
  }

  &__created-box {
    display: flex;
    align-items: center;
    max-width: 128px;
  }

  &__social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
  }

  &__social-ico {
    fill: #ffffff;
  }

  &__ico-academy {
    fill: #000000;
  }

  &__social-link,
  &__created-link {
    display: inline-flex;
  }

  &__created-text {
    @include use.font-normal;
    text-transform: uppercase;
    margin: 0;
    margin-right: 12px;
  }
}
